import Header from './components/Header'
import Task from './components/Task'
import './App.css'
import { useState } from "react";
import AddTask from './components/AddTask'

function App() {

  const [showAdd,setshowAdd] = useState(false)

  const [task,setTask] = useState([
    {
        id: 1,
        text: 'Hair Appointment',
        day: 'Feb 5th at 2:30pm',
        reminder: true,
    },
    {
        id: 2,
        text: 'Car Sevice Appointment',
        day: 'Feb 6th at 6:30pm',
        reminder: true,
    },
    {
        id: 3,
        text: 'Food Shopping',
        day: 'Feb 5th at 12:30pm',
        reminder: true,
    },
])

const deleteTask = (id) =>{
  setTask(task.filter((task) => task.id !== id))
}

const addTask = (ta) =>{
  const id= Math.floor(Math.random()* 10000)+1
  const newTask = { id, ...ta}
  setTask([...task,newTask])
}

const toggleRemainder = (id) =>{
  setTask(
    task.map((t) => t.id === id ? { ...t, reminder: !t.reminder} : t))
}

  return (
    <div class="front-data">
      <Header title="Task Manger" onAdd={()=>setshowAdd(!showAdd) } showAddTask={showAdd} />
      {showAdd && <AddTask onAdd={addTask} />}
      { task.length > 0 ? <Task task={task} onDelete={deleteTask} onToggle={toggleRemainder} /> : <h3>No task found</h3>} 
    </div>
  );
}

export default App;
