import '../App.css'

const Header = ({title, onAdd, showAddTask }) => {
    return (
        <header>
            <h1 class="headding" >{title}
                <input class={showAddTask ? 'btncl' : 'btn'} type="button" onClick={onAdd} value={showAddTask ? 'Close' : 'Add'}/>
            </h1>
        </header>
    );
}

export default Header
